
.cartcontrol {
  font-size: 0;
}
.cartcontrol .cart-decrease,
  .cartcontrol .cart-add {
    display: inline-block;
    padding: 6px;
    line-height: 24px;
    font-size: 24px;
    color: #00a0dc;
}
.cartcontrol .cart-decrease {
    opacity: 1;
    -webkit-transform: translate3D(0, 0, 0) rotate(0);
            transform: translate3D(0, 0, 0) rotate(0);
}
.cartcontrol .cart-decrease.move-enter-active, .cartcontrol .cart-decrease.move-leave-active {
      -webkit-transition: all .4s linear;
      transition: all .4s linear;
}
.cartcontrol .cart-decrease.move-enter, .cartcontrol .cart-decrease.move-leave-active {
      opacity: 0;
      -webkit-transform: translate3D(24px, 0, 0) rotate(180deg);
              transform: translate3D(24px, 0, 0) rotate(180deg);
}
.cartcontrol .cart-count {
    display: inline-block;
    vertical-align: top;
    width: 12px;
    padding-top: 6px;
    line-height: 24px;
    text-align: center;
    font-size: 12px;
    color: #93999f;
}



